// yarn add @mui/material @emotion/react @emotion/styled @mui/icons-material
// yarn remove @mui/material @emotion/react @emotion/styled @mui/icons-material

import {
  Box,
  ButtonGroup,
  Button,
  IconButton,
  Typography,
  styled,
  Paper,
  Divider,
  Link,
} from "@mui/material";
import { Google } from "@mui/icons-material";
import { useTheme } from "@emotion/react";

export function MUI_PCS() {
  useTheme();
  const RowBox = styled(Box)({
    display: "flex",
    flexDirection: "row",
    justifyContent: "space-around",
    margin: "1rem 0",
  });

  function IconButtonWithoutProps() {
    return (
      <IconButton>
        <Google />
      </IconButton>
    );
  }

  function ButtonWithoutProps() {
    return (
      <Button>
        <Google />
      </Button>
    );
  }

  function IconButtonWithProps(iconProps: any) {
    return (
      <IconButton {...iconProps}>
        <Google />
      </IconButton>
    );
  }
  function IconButtonWithPropsWithOnClick(iconProps: any) {
    return (
      <IconButton
        onClick={() => {
          alert("useless click");
        }}
        {...iconProps}
      >
        <Google />
      </IconButton>
    );
  }
  return (
    <Box>
      <Typography variant="h3">
        Props of elements in <code>ButtonGroup</code> is changing style.
      </Typography>
      I don't think it's intentional, hence it's a misbehavior/bug.
      <Divider sx={{ m: 2 }} />
      {/* 
      Divider
      */}
      <Typography variant="h4">
        Property <code>href</code> changes the right border of the element
      </Typography>
      If <code>href</code> defined, an a element will be used as the root node.
      See <Link href="https://mui.com/api/button/#props">official doc</Link>.{" "}
      Wrapping element will change to <code>{"<a />"}</code>. I think{" "}
      <code>{"<a />"}</code> and <code>{"<div />"}</code> should have more
      consistent behavior.
      <RowBox>
        <Paper>
          <ButtonGroup>
            <Button href="/">btn</Button>
            <IconButton>
              <Google />
            </IconButton>
          </ButtonGroup>
          <br />
          Button with href
        </Paper>
        <Paper>
          <ButtonGroup>
            <Button>btn</Button>
            <IconButton>
              <Google />
            </IconButton>
          </ButtonGroup>
          <br />
          Button without href
        </Paper>
      </RowBox>
      <Divider sx={{ m: 2 }} />
      {/* 
      Divider
      */}
      <Typography variant="h4">
        Modularizing <code>IconButton</code> changes color and hover style of
        element.
      </Typography>
      All <code>IconButton</code>-s are in <code>{"<ButtonGroup />"}</code>
      <RowBox>
        <Paper>
          <ButtonGroup>
            <IconButton>
              <Google />
            </IconButton>
            <IconButton>
              <Google />
            </IconButton>
            <Button href="/">btn</Button>
            <IconButton>
              <Google />
            </IconButton>
            <IconButton>
              <Google />
            </IconButton>
          </ButtonGroup>
          <br />
          With <code>IconButton</code> and <code>{"<MUI_ICON>"}</code>, all
          good.
        </Paper>
        <Paper>
          <ButtonGroup>
            <IconButtonWithoutProps />
            <IconButtonWithoutProps />
            <Button href="/">btn</Button>
            <IconButtonWithoutProps />
            <IconButtonWithoutProps />
          </ButtonGroup>
          <br /> Using a new customized component.
          <br /> Misbehavior: buttons are white and not in group.
          <br /> See the hover highlight of second and third "G"
          <br /> The component is based on <code>IconButton</code> without
          passing <code>{"{...props}"}</code>.
        </Paper>
      </RowBox>
      <Divider sx={{ m: 2 }} />
      {/* 
      Divider
      */}
      <Typography variant="h4">
        Passing empty <code>props</code> changes color and hover style of
        element.
      </Typography>
      Two work-around-s to get the color back.
      <RowBox>
        <Paper>
          <ButtonGroup>
            <IconButtonWithProps />
            <IconButtonWithProps />
            <Button href="/">btn</Button>
            <IconButtonWithProps />
            <IconButtonWithProps />
          </ButtonGroup>
          <br /> Using a new customized component. All good.
          <br /> The component is also based on <code>IconButton</code> but
          passing <code>{"{...props}"}</code>.
          <br /> Although the <code>props</code> is not passed, hence should be
          empty(?).
          <br /> Maybe <code>useStyle</code> changed color, but the hover
          background shape is changed too.
        </Paper>
        <Paper>
          <ButtonGroup>
            <ButtonWithoutProps />
            <ButtonWithoutProps />
            <Button href="/">btn</Button>
            <ButtonWithoutProps />
            <ButtonWithoutProps />
          </ButtonGroup>
          <br /> Using a new customized component. Color is good.
          <br /> This time the component is based on <code>Button</code>, still
          not passing <code>{"{...props}"}</code>.
          <br /> The hover background style(shape)
        </Paper>
      </RowBox>
      <Divider sx={{ m: 2 }} />
      {/* 
      Divider
      */}
      <Typography variant="h4">
        More tests with customized <code>IconButton</code> with{" "}
        <code>props</code>.
      </Typography>
      <RowBox>
        <Paper>
          <ButtonGroup>
            <IconButtonWithProps />
            <IconButtonWithProps />
            <IconButtonWithProps />
            <Button href="/">btn</Button>
            <Button href="/">btn</Button>
          </ButtonGroup>
          <br />
          All <code>IconButton</code>-s are on one side.
          <br />
          Incoherent behavior but I like:
          <br /> The hover background of last <code>IconButton</code> will
          change.
        </Paper>
        <Paper>
          <ButtonGroup>
            <IconButtonWithProps />
            <IconButtonWithProps />
            <Button>btn</Button>
            <IconButtonWithProps />
            <IconButtonWithProps />
          </ButtonGroup>
          <br />
          Element <code>{"<Button />"}</code> with no <code>href</code>
          <br /> Misbehavior: button missing right border.
        </Paper>
        <Paper>
          <ButtonGroup size="large">
            <IconButtonWithProps />
            <IconButtonWithProps />
            <Button href="/">btn</Button>
            <IconButtonWithProps />
            <IconButtonWithProps />
          </ButtonGroup>
          <br />
          Large size. All good.
        </Paper>
        <Paper>
          <ButtonGroup size="large">
            <IconButtonWithPropsWithOnClick />
            <IconButtonWithPropsWithOnClick />
            <Button href="/">btn</Button>
            <IconButtonWithPropsWithOnClick />
            <IconButtonWithPropsWithOnClick />
          </ButtonGroup>
          <br />
          With <code>onClick</code>. All good.
        </Paper>
      </RowBox>
      <Divider sx={{ m: 2 }} />
      {/* 
      Divider
    */}
      <Typography variant="h4">
        More about <code>IconButton</code> with <code>ButtonGroup</code>.
      </Typography>
      <code>IconButton</code> (both MUI original and customized) in{" "}
      <code>ButtonGroup</code> will also cause two <code>Error/Warning</code>-s:
      <pre>
        <li>
          Warning: React does not recognize the `disableElevation` prop on a DOM
          element. If you intentionally want it to appear in the DOM as a custom
          attribute, spell it as lowercase `fullwidth` instead. If you
          accidentally passed it from a parent component, remove it from the DOM
          element.
        </li>
      </pre>
      <pre>
        <li>
          Warning: React does not recognize the `disableElevation` prop on a DOM
          element. If you intentionally want it to appear in the DOM as a custom
          attribute, spell it as lowercase `disableelevation` instead. If you
          accidentally passed it from a parent component, remove it from the DOM
          element.
        </li>
      </pre>
      <RowBox></RowBox>
    </Box>
  );
}
